.ubc-accordion-tabs__tab-list li a[role=tab].is-selected, .ubc-accordion-tabs__tab-list li button[role=tab].is-selected {
  background: var(--ubc-tabs-block-tab-nav-selected-background, #ffffff);
  color: var(--ubc-tabs-block-tab-nav-selected-text-color, #0075af);
  opacity: 1;
}
.ubc-accordion-tabs__tab-list li a[role=tab]:focus, .ubc-accordion-tabs__tab-list li button[role=tab]:focus {
  outline: none;
